*{
    margin: 0;
    padding: 0;
    border: none;
    outline: 0;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
}

body{
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background: none;
}

:root{
    --bg-greeting: #05092e;
}

.greeting-container{
    width: 100%;
    display: flex;
    background-color: var(--bg-greeting);
}